* {
  box-sizing: border-box;
}

html {
  font-family: "Roboto", open-sans;
}

body {
  margin: 0;
}

header {
  background: #E4C1AE;
  color: white;
  padding: 1rem;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  width: 100%;
}
header h1 {
  font-size: 4rem;
  color: white;
  margin: 0;
  text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2), 0 20px 20px rgba(0, 0, 0, 0.15);
}

section {
  display: flex;
  justify-content: center;
  width: 1440px;
  margin-top: 95px;
}
section div {
  width: 380px;
  margin-left: 95px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
section div h1 {
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
  margin: 25px 0;
}
section div p {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  margin: 0;
  height: 95px;
}
section div button {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  width: 130px;
  height: 50px;
  background: #E4C1AE;
  border-radius: 32px;
  color: #FFFFFF;
  border: none;
  margin-top: 35px;
}